@media (max-width: 1200px) {
    .container {
        width: 100%;
    }
    .search {
        width: 18%;
        margin: 7px 21px 0 0px;
    }
    .input {
        width: 40%;
        top: -4px;
    }
    .overlay-1 {
        height: 40px;
        left: 46px;
        top: -7px;
        width: 40px;
    }
    .overlay-2 {
        height: 38px;
        left: 102px;
        top: -7px;
        width: 38px;
    }
    svg {
        width: 78%;
    }
    .language {
        &__contact {
            margin-top: -30px;
        }
    }
    .top {
        &__header {
            font-size: 55px;
        }
        &__subheader {
            font-size: 20px;
        }
    }
    .services {
        &__header {
            text-align: center;
        }
        &__wrapper {
            display: grid;
            grid-template-columns: repeat(2, 315px);
            justify-content: center;
            gap: 20px;
        }
        &__item {
            box-shadow: 0 6px 5px -2px rgba(0,0,0,.2);
            &__location {
                margin-left: 55px;
            }
            &__btn {
                margin-left: 61px;
            }
        }
        #red {
            width: 100%;
            &__header {
                padding-left: 72px;
            }
        }
    }
    .middle {
        &__item {
            &__picture {
                width: 100%;
            }
        }
    }
    .logos {
        &__picture {
            width: 100%;
        }
    }
    .footer {
        &__picture {
            &_left {
                width: 25%;
            }
            &_right {
                width: 25%;
            }
        }
        &__wrapper {
            margin: -176px 0 0 271px;
            gap: 33px;
        }
        &__item {
            &__header {
                font-size: 14px;
                background: transparent;
            }
            &__li {
                font-size: 12px;
            }
        }
    }
}

@media (max-width: 991px) {
    .menu {
        &__wrapper {
            margin-left: 113px;
        }
    }
    .search {
        width: 23%;
        margin: -111px 311px 0 0;
    }
    .input {
        width: 50%;
        left: 8px;
        background: transparent;
    }
    .top {
        &__header {
            font-size: 45px;
        }
    }
    .middle {
        &__header {
            font-size: 49px;
        }
        &__item {
            &__list {
                padding-left: 4px;
            }
        }
    }
    .excellence {
        &__descr {
            width: 565px;
        }
    }
    .footer {
        &__wrapper {
            margin: -132px 0 0 180px;
        }
        &__item {
            &__li {
                font-size: 11px;
            }
        }
    }
}

@media (max-width: 767px) {
    .search {
        margin: -104px 237px 0 0;
    }
    .overlay-1 {
        height: 32px;
        left: 33px;
        top: -2px;
