            &__location {
                margin-left: 67px;
            }
        }
        #red {
            margin-top: 35px;
            &__header {
                padding-left: 193px;
            } 
        }
    }
    .middle {
        margin-top: 75px;
        &__header {
            font-size: 41px;
        }
        &__item {
            &__header {
                font-size: 13px;
            }
            &__list {
                font-size: 12px;
            }
        }
    }
    .tab {
        &__picture {
            display: none;
        }
    }
    .excellence {
        height: 430px;
        &__btn {
            margin: 35px 0 0 176px;
            float: none;
        }
    }
    .footer {
        &__wrapper {
            margin: -107px 0 0 127px;
        }
        &__item {
            &__header {
                font-size: 10px;
            }
            &__li {
                font-size: 9px;
            }
        }
    }
}

