.footer {
    &__picture {
        &_left {
            position: relative;
        }
        &_right {
            position: relative;
            float: right;
        }
    }
    &__wrapper {
        position: absolute;
        margin: -278px 0 0 400px;
        display: flex;
        gap: 126px;
    }
    &__item {
        &__header {
            border: none;
            background-color: white;
            font-family: Poppins;
            font-size: 20px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            text-align: left;
            color: #ba0000;
            background: transparent;
            &:hover {
                color: #fc0;
            }  
        }
        &__ul {
            display: none;
            padding: 0 0 0 0;
        }
        &__li {
            font-family: Poppins;
            font-size: 14px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            text-align: left;
            line-height: 1.71;
            list-style-type: none;
            color: #777777;
            
            &:hover {
                color: #fc0;
            }
            &:before {
                color: #ba0000;
                content: "🢖 ";   
            }
        }
    }
}

.active-style {
    display: block;
}
.active-content {
    display: block;
}





