.top {
    background: url('../img/bg/bg_top.jpg') center center / cover no-repeat;
    &__header {
        width: 55%;
        height: 155px;
        margin-top: 179px;
        display:inline-block;
        font-family: Poppins;
        font-size: 70px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.14;
        letter-spacing: 2.1px;
        text-align: left;
        color: #ffffff;
    }
    &__subheader {
        width: 727px;
        margin-top: 20px;
        margin-bottom: 40px;
        font-family: Poppins;
        font-size: 24px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.5;
        letter-spacing: 0.6px;
        text-align: left;
        color: #ffcc00;
    }
    &__options {
        font-family: Poppins;
        font-size: 20px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.5;
        letter-spacing: 0.6px;
        text-align: left;
        color: #ffffff;
    }
    &__btn {
        &_right {
        width: 192px;
        height: 50px;
        margin: 40px 64px 140px 20px;
        padding: 15px 37px 20px;
        border-radius: 25px;
        border: solid 2px #ffffff;
        font-family: Poppins;
        font-size: 12px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        background: transparent;
        color: white;
        &:hover {
            background-color: #ffcc00;
            color: #ba0000;
        }
    }
        &_left {
            width: 192px;
            height: 50px;
            padding: 15px 37px 20px;
            border-radius: 25px;
            border: solid 2px #ffffff;
            font-family: Poppins;
            font-size: 12px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            text-align: center;
            background: transparent;
            color: white;
            &:hover {
                background-color: #ffcc00;
                color: #ba0000;
            }  
        }
    }
}