.modal__overlay {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0,0,0, .56);
    &__thanks {
        display: none;
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        background-color: rgba(0,0,0, .56);
    }    
}
.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 380px;
    height: 60px;
    background-color: white;
    display: none;
    &__services {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 380px;
        height: 420px;
        background-color: white;
    }
    &__thanks {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 380px;
        height: 85px;
        background-color: white; 
    }
    &__title {
        margin-top: 15px;
        font-family: Poppins;
        font-size: 20px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #5f5f5f;  
    }
    &__subtitle {
        margin-top: 12px;
        font-family: Poppins;
        font-size: 20px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #5f5f5f;
    }
    &__close {
        position: absolute;
        top: -14px;
        right: 5px;
        font-size: 36px;
        cursor: pointer;
    }
    &__name {
        width: 66%;
        height: 12%;
        display: inline-block;
        margin: 42px 0 0 54px;
        border: none;
        border-bottom: 1px solid rgb(104, 95, 95);
        
    }
    &__email {
        width: 66%;
        height: 12%;
        display: inline-block;
        margin: 25px 0 0 54px;
        border: none;
        border-bottom: 1px solid rgb(104, 95, 95);
    }
    &__message {
        width: 66%;
        height: 12%;
        display: inline-block;
        margin: 25px 0 0 54px;
        border: none;
        border-bottom: 1px solid rgb(104, 95, 95);
    }
    &__btn {
        width: 160px;
        height: 59px;
        margin-top: 118px;
        margin-bottom: 93px;
        margin-left: 79px;
        width: 192px;
        height: 50px;
        padding: 15px 37px 20px;
        border-radius: 25px;
        border: solid 2px #ffffff;
        font-family: Poppins;
        font-size: 12px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        background-color: #ffcc00;
        color: white;
        &:hover {
            color: #ba0000;
        }
    }
}