.tab {
    margin-top: 250px;
    margin-bottom: 100px;
    &__wrapper {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    &__picture {
        margin-top: 94px;
        width: 39%;
    }
    &__item {
        width: 414px;
        height: 500px;
        margin-top: 100px;
        padding: 0 0 40px 41px;
        background-color: #ba0000; 
        &__header {
            display: inline-block;
            margin: 0 0 60px 45px;
            padding-top: 13px;
            font-family: Poppins;
            font-size: 14px;
            font-weight: 400;
            font-stretch: normal;
            font-style: normal;
            cursor: pointer;
            text-align: center;
            color: #fff;
            &__active {
                display: inline-block;
            }
            
        }
        #yellow {
            width: 203px;
            height: 50px;
            margin-left: 64px;
            box-shadow: 0px 10px 18px 0 rgba(255, 204, 0, 0.4);
            background-color: #fc0;
            &:active {
                background-color: #ba0000; 
                box-shadow: none;
            }
            &:active {
                background-color: #ba0000; 
            }
        }
        &__content {
            display: none;
            &__descr {
                &__select {
                    width: 343px;
                    height: 35px;
                    margin-top: 20px;
                    padding-left: 10px;
                    cursor: pointer;
                    background-color: #ba0000;
                    color:#ffffff;
                  
                }
            }
        }
        &__btn {
            width: 192px;
            height: 50px;
            margin: 55px 0px 0px 68px;
            padding: 9px 37px 20px;
            border-radius: 25px;
            border: solid 2px #fff;
            font-family: Poppins;
            font-size: 18px;
            font-weight: 700;
            font-stretch: normal;
            font-style: normal;
            text-align: center;
            background: 0 0;
            color: #fff;
            &:hover {
                background-color: #ffcc00;
                color: #ba0000;
            }
        }
    }
}
.show {
    display: inline-block;
}
.hide {
    display: none;
}