.excellence {
    height: 350px;
    background-color: #ba0000;
    &__header {
        padding-top: 100px;
        font-family: Poppins;
        font-size: 30px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        text-align: left;
        text-transform: uppercase;
        color: #ffffff;
    }
    &__descr {
        width: 739px;
        padding-top: 41px;
        font-family: Poppins;
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        text-align: left;
        color: #ffffff;
    }
    &__btn {
        width: 192px;
        height: 50px;
        margin-top: -100px;
        padding: 15px 37px 20px;
        border-radius: 25px;
        border: solid 2px #ffffff;
        font-family: Poppins;
        font-size: 12px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        background: transparent;
        color: white;
        float: right;
        &:hover {
            background-color: #ffcc00;
            color: #ba0000;
        }
    }
}