.language {
    height: 50px;
    background-color: #ba0000;
    &__choose {
        width: 85px;
        height: 45px;
        margin: 1px 819px 0 0;
        font-family: Poppins;
        font-size: 14px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        border: none;
        cursor: pointer;
        background-color: #ba0000;
        color: #ffffff;
    }
    &__contact {
        margin-top: 15px;
        font-family: Poppins;
        font-size: 14px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        float: right;
        cursor: pointer;
        color: #ffffff;
        &:hover {
            text-decoration: underline;
        }
    }
}