.services {
    &__header {
        margin-top: 134px;
        margin-bottom: 114px;
        font-family: Poppins;
        font-size: 50px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.6;
        letter-spacing: 1.5px;
        text-align: left;
        color: #ba0000;
    }
    &__wrapper {
        display: flex;
    }
    &__item {
        &__header {
            font-family: Poppins;
            font-size: 20px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 4;
            letter-spacing: 0.6px;
            text-align: left;
            color: #ba0000;
        }
        &__descr {
            font-family: Poppins;
            font-size: 16px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            text-align: left;
            color: #777777;
        }
        &__read {
            font-family: Poppins;
            font-size: 16px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.63;
            letter-spacing: 0.48px;
            text-align: left;
            color: #222222;
        }
        &__location {
            width: 200px;
            height: 55px;
            margin-top: 39px;
            margin-left: 25px;
            padding: 2px;
            background-color: #ba0000;
            font-family: Poppins;
            font-size: 16px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 5;
            letter-spacing: 0.48px;
            text-align: left;
            color: #ffffff;
        }
        &__btn {
            width: 192px;
            height: 50px;
            margin-top: 20px;
            margin-left: 30px;
            padding: 15px 37px 20px;
            border-radius: 25px;
            border: solid 2px #ffffff;
            font-family: Poppins;
            font-size: 12px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            text-align: center;
            background: transparent;
            color: white;
            &:hover {
                background-color: #ffcc00;
                color: #ba0000;
            } 
        }
    }
    #red {
        width: 50%;
        background-color: #ba0000;
        &__header {
            padding-left: 50px;
            font-family: Poppins;
            font-size: 20px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 4;
            letter-spacing: 0.6px;
            text-align: left;
            color: #ffffff;
        }
        &__descr {
            margin-top: 30px;
            font-family: Poppins;
            font-size: 16px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            text-align: center;
            color: #ffffff;
        }
    }
    
}
