@import 'libs/bootstrap-reboot.min';
@import 'libs/fonts';

* {
    font-family: 'Ubuntu', sans-serif;
    font-family: 'UniNeue', sans-serif;
}

hr {
    width: 44%;
    height: .5%;
    border: none;
    background-color: #fc0;
}

html {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
}

body {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
}

.container {
    width: 1140px;
    height: 100%;
    margin: 0 auto;
}

.logos {
    margin: 70px 0 70px 0;
    &__wrapper {
        display: flex;
        gap: 20px;
    }
    &__picture {
        &:hover {
            border: solid 1px #ba0000;
            margin: -1px;
        }
    }
}

.fade-in {
    animation: fadeIn ease 1s;
    -webkit-animation: fadeIn ease 1s;
    -moz-animation: fadeIn ease 1s;
    -o-animation: fadeIn ease 1s;
    -ms-animation: fadeIn ease 1s;
}
  
@keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

@-moz-keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

@-o-keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

@-ms-keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

