.middle {
    margin-top: 150px;
    height: 700px;
    position: relative;
    background: url('../img/bg/bg_middle.jpg') center center/cover no-repeat;
    &__header {
        padding-top: 150px;
        font-family: Poppins;
        font-size: 50px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #ffffff;
    }
    &__wrapper {
        display: flex;
        position: absolute;
        margin-top: 275px;
        gap: 30px;
    }
    &__item {
        box-shadow: 0 6px 4px 6px rgba(0, 0, 0, .2);
        &__header {
            padding: 17px 0 30px 27px;
            font-family: Poppins;
            font-size: 20px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            text-align: left;
            color: #ba0000;
        }
        &__list {
            font-family: Poppins;
            font-size: 16px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            text-align: left;
            color: #777777;
            
            &__descr {
                list-style-type: none;
                &:before {
                    color: #ffcc00;
                    content: "🢖 ";
                    
                }
            }
        }        
    }
}

